:root {
  --main-bgc-color: #fff;
  --second-bgr-color: #191c26;
  --text-main-color: #303030;
  --accent-color: #ff6c00;
  --main-btn-color: #fff;
  --second-btn-color: #303030;
  --btn-hover: #ff6c00;
  --text-hover: #ff6c00;
  --main-pre-title-color: #303030;
  --second-pre-title-color: rgba(157, 164, 189, .6);
  --pre-title-font-size: 11px;
  --main-title-color: #fff;
  --second-title-color: #303030;
  --title-font-size: 42px;
  --main-sub-title-color: #555;
  --second-sub-title-color: #9da4bd;
  --sub-title-font-size: 18px;
  --main-contacts-text-color: #9da4bd;
  --second-contacts-text-color: rgba(157, 164, 189, .4);
  --feature-text-color: #303030;
  --feature-text-font-size: 11px;
  --feature-number-size: 42px;
  --feature-number-color: #303030;
}

body {
  font-family: Open Sans, sans-serif;
}

* {
  margin: 0;
  font-size: 14px;
  font-style: normal;
  line-height: 1.33;
  text-decoration: none;
  list-style: none;
}

.header {
  background-color: rgba(8, 10, 15, .392);
  padding-right: 10px;
  display: flex;
}

.header-text {
  color: #fff;
  letter-spacing: .1em;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.33;
}

.header-logo {
  margin-right: 70px;
}

.header-nav-list {
  margin-right: 320px;
  padding-left: 0;
  display: flex;
}

.header-navList-item:not(:last-child) {
  margin-right: 40px;
}

.header-navList-link:hover, .header-navList-link:focus, .header-tel:hover, .header-tel:focus {
  color: var(--accent-color);
}

.header-tel {
  margin-right: 40px;
}

.pre-title {
  font-size: var(--pre-title-font-size);
  color: var(--main-pre-title-color);
  text-transform: uppercase;
}

.hero .pre-title {
  color: var(--second-pre-title-color);
}

.contacts .pre-title {
  color: #fff;
}

.section-header {
  font-family: Raleway, sans-serif;
  font-size: var(--title-font-size);
  color: var(--second-title-color);
  letter-spacing: 2.1px;
  font-weight: 700;
  line-height: 1.33;
}

.hero-header {
  letter-spacing: 3.6px;
  font-size: 72px;
  font-weight: 700;
  line-height: 1.33;
}

.sub-title {
  font-size: var(--sub-title-font-size);
  color: var(--main-sub-title-color);
}

.hero .sub-title, .prices .sub-title, .contacts .sub-title {
  color: var(--second-sub-title-color);
}

.text-description {
  color: var(--main-sub-title-color);
  letter-spacing: .28px;
  font-size: 14px;
  line-height: 1.33;
}

.btn {
  width: 160px;
  height: 40px;
  border: 1px solid var(--accent-color);
  letter-spacing: 1.08px;
  text-transform: uppercase;
  color: var(--main-btn-color);
  border-radius: 25px;
  padding: 12px 20px;
  font-family: inherit;
  font-size: 12px;
}

.btn:hover, .btn:focus {
  background-color: var(--btn-hover);
}

.about .btn {
  color: var(--second-btn-color);
}

.feature-text {
  color: var(--feature-text-color);
  font-size: var(--feature-text-font-size);
  font-weight: 600;
  line-height: 1.33;
}

.feature-number {
  color: var(--feature-number-color);
  font-size: var(--feature-number-size);
  letter-spacing: 2.1px;
  font-weight: 700;
}

.barber-name {
  color: var(--text-main-color);
  letter-spacing: .36px;
  font-size: 18px;
  font-weight: 400;
}

.barber-skill {
  color: var(--accent-color);
  letter-spacing: .28px;
  font-size: 14px;
}

